import image1 from '../../src/assets/images/NewsStorage/1.jpg'
import image2 from '../../src/assets/images/NewsStorage/2.jpg'
import image3 from '../../src/assets/images/NewsStorage/3.jpg'
import image4 from '../../src/assets/images/NewsStorage/4.jpg'

const PUSH_POST_TO_WALL = 'react-app/news/PUSH-POST-TO-WALL'
const SAVE_TEMP_IMAGE = 'react-app/news/SAVE-TEMP-IMAGE'
const DELETE_TEMP_IMAGE = 'react-app/news/DELETE-TEMP-IMAGE'
const PUSH_A_FULL_POST_TO_WALL = 'react-app/news/PUSH-A-FULL-POST-TO-WALL'

const images = {
    image1,
    image2,
    image3,
    image4
}

let initialState = {
    posts: [
        {
            id: 1,
            userName: 'JSProgrammer',
            text: 'Aesthetics is a philosophical teaching about the essence and forms of beauty in artistic creativity, in nature and in life, about art as a special form of social consciousness. Every person have our look on aesthetics.',
            image: images.image1,
            likesCount: 12
        },
        {
            id: 2,
            userName: 'ReactLearner',
            text: 'Books are the best carrier of knowledge!',
            image: images.image2,
            likesCount: 23
        }
    ],
    tempImage: null
}

const newsReducer = (state = initialState, action) => {
    switch (action.type) {
        case PUSH_POST_TO_WALL:
            return {
                ...state,
                posts: [...state.posts, { id: 3, postText: action.formData, userName: action.login }]
            }
        case SAVE_TEMP_IMAGE:
            return {
                ...state,
                tempImage: action.tempImage
            }
        case DELETE_TEMP_IMAGE:
            return {
                ...state,
                tempImage: null
            }
        case PUSH_A_FULL_POST_TO_WALL:
            return {
                ...state,
                posts: [...state.posts, { id: 3, postText: action.formData, userName: action.login, image: state.tempImage }]
            }
        default:
            return state
    }
}

export const pushPostToWall = (formData, login) => ({ type: PUSH_POST_TO_WALL, formData, login })
export const saveTempImage = (tempImage) => ({ type: SAVE_TEMP_IMAGE, tempImage })
export const deleteTempImage = () => ({ type: DELETE_TEMP_IMAGE })
export const pushAFullPostToWall = (formData, login) => ({ type: PUSH_A_FULL_POST_TO_WALL, formData, login })


export default newsReducer