const SEND_MESSAGE = 'react-app/dialogs/SEND-MESSAGE'
const SET_DIALOG_ID = 'react-app/dialogs/SET-DIALOG-ID'

let initialState = {
    dialogs: [
        {
            id: 0,
            name: 'Aleksandr',
            messages: [
                { id: 0.1, message: 'Hi Aleksandr' },
                { id: 0.2, message: 'How are things?' }
            ]
        },
        {
            id: 1,
            name: 'Ivan',
            messages: [
                { id: 1.1, message: 'Hi Ivan' },
                { id: 1.2, message: 'How are things?' }
            ]
        },
    ],
    dialogId: null,
    users: {
        usersForArray: [
            { id: 0, name: 'Aleksandr' },
            { id: 1, name: 'Ivan' }
        ]
    },
    messages: {
        messagesForArray: [
            [
                { id: 0.1, message: 'Hi Aleksandr' },
                { id: 0.2, message: 'How are things?' }
            ],
            [
                { id: 1.1, message: 'Hi Ivan' },
                { id: 1.2, message: 'How are things?' }
            ]
        ]
    }
}

const dialogsReducer = (state = initialState, action) => {

    switch (action.type) {
        case SET_DIALOG_ID:
            return {
                ...state,
                dialogId: action.dialogId //Хуйня с этим копированием :с
            }
        case SEND_MESSAGE: {
            let body = action.newMessageBody
            let dialogId = state.dialogId
            return {
                ...state,

                messages: [...state.messages.messagesForArray[state.dialogId], { id: dialogId, message: body }],
            }
        }
        default:
            return state
    }
}

export const sendMessageCreator = (newMessageBody) => ({ type: SEND_MESSAGE, newMessageBody })
export const setDialogId = (dialogId) => ({ type: SET_DIALOG_ID, dialogId })

export default dialogsReducer