const SET_USER_API_KEY = 'react-app/auth/SET-USER-API-KEY'

let initialState = {
    APIKey: null
}

const apiReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_API_KEY:
            return {
                ...state,
                APIKey: action.APIKey
            }
        default:
            return state
    }
}

export const setUserAPIKey = (APIKey) => ({ type: SET_USER_API_KEY, APIKey })

export const setUserAPIKeyThunk = (APIKey) => async (dispatch) => {
    console.log('sadadsd')
    await dispatch(setUserAPIKey(APIKey))
    localStorage.setItem('APIKey', APIKey);
}

export default apiReducer